html,
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  font-family: 'Noto Sans', sans-serif;
  font-weight: 400;
  font-stretch: normal;
  font-size: 14px;
  line-height: 1.8;
}

h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 36px;
  line-height: 1.2;
  letter-spacing: 5px;
  font-weight: 400;
  text-transform: capitalize;
}

button {
  margin: 0 auto;
  position: relative;
  font-weight: 400;
  font-stretch: normal;
  font-size: 14px;
  line-height: 1.2;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
  padding: 7px 20px;
  border: none;
  background: transparent;
  color: #fff;
  z-index: 0;

}

button:hover {
  transition: all 0.4s ease-in-out 0s;
  background-color: #4e92e5;
  width: 170px;
}

button:after {
  position: absolute;
  content: "";
  height: 6px;
  width: 50%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #4e92e5;
  margin: 0 auto;
  z-index: -1;
}

a{
  color: #fff;
  text-decoration: none;
}

a:hover {
  color: #4e92e5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}