
.skills {
    min-height: 100vh;
    width: 100%;
    background-color: #12151a;
    display: flex;
    justify-content: center;
    text-align: center;
}

.container {
    width: 80%;
    flex-direction: column;
    align-items: center;
}

.mySkills {
    height: 200px;
}

.allSkill {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}


.line {
    background: #4e92e5;
    height: 1px;
    width: 100px;
    margin: 0 auto;
}



.item {
    transform: rotate(-45deg);
}